import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);
const routes = [
  {
    path: '/echarts',
    name: 'ECHARTS',
    component: () => import('@/views/echarts/index'),
  },
  {
    // path: '/plan-view',
    path: '/',
    name: 'ECHARTS_VIEW',
    component: () => import('@/views/echarts-view/index'),
  },
  // 测试文件
  {
    path: '/test',
    name: 'test',
    component: () => import('@/views/test/index'),
  },
];
const router = new VueRouter({
  mode: 'history',
  routes,
});
export default router;
